<template>
  <div class="view-mark" @click.self="$emit('close')">
    <Loader v-if="loading" />
    <div v-else class="card">
      <h5>Информация о задании</h5>
      <table>
        <tbody>
          <tr>
            <td><b>Дата:</b> {{ date | dateFormat }}</td>
          </tr>
          <tr>
            <td><b>Вес:</b> {{ weight }}</td>
          </tr>
          <tr>
            <td><b>Оценка:</b> {{ mark }}</td>
          </tr>
          <tr>
            <td><b>Учитель:</b> {{ teacher }}</td>
          </tr>
          <tr>
            <td><b>Предмет:</b> {{ subject }}</td>
          </tr>
          <tr>
            <td><b>Тип задания:</b> {{ type }}</td>
          </tr>
          <tr>
            <td @click="showTheme"><b>Тема задания:</b> {{ theme }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import Loader from "@/components/Loader";

export default {
  props: {
    id: {
      type: Number,
      default: null,
      required: true
    },
    mark: {
      type: Number,
      default: null,
      required: true
    },
    type: {
      type: String,
      default: null,
      required: true
    }
  },
  data: () => ({
    loading: true,
    date: null,
    weight: null,
    teacher: "",
    subject: "",
    theme: ""
  }),
  methods: {
    ...mapActions(["GetMark"]),
    showTheme(e) {
      e.target.classList.toggle("show");
    }
  },
  filters: {
    dateFormat(val) {
      if (!val) return "";
      function num(val) {
        if (val < 10) val = "0" + val;
        return val;
      }
      return `${num(val.getDate())}.${num(val.getMonth() + 1)}.${num(
        val.getFullYear() % 100
      )}`;
    }
  },
  created() {
    setTimeout(() => {
      if (this.id == null || this.mark == null || this.type == null) {
        this.$emit("close");
        return;
      }
      this.GetMark({ id: this.id })
        .then(
          ({ assignmentName, date, subjectGroup, teacher, weight }) => (
            (this.date = new Date(date)),
            (this.subject = subjectGroup.name),
            (this.teacher = teacher.name),
            (this.theme = assignmentName),
            (this.weight = weight),
            (this.loading = false)
          )
        )
        .catch(err => {
          this.loading = false;
          if (err.code == 4008) {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Сайт лег, попробуй позже" });
          } else if (err.code == 4006) {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Ошибка парсера, попробуй ещё раз" });
          } else {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Неизвестная ошибка, попробуй ещё раз" });
          }
          console.error(err);
        });
    }, 0);
  },
  components: {
    Loader
  }
};
</script>
